/* eslint-disable eqeqeq */
import React, { Component } from 'react'
import axios from 'axios';
import firebase from "./FireBaseConfig"
import "@firebase/database"
import Navbar from './Navbar'
import Setting from './Setting'
import Ayah from './Ayah'
import { RightOutlined, LeftOutlined } from '@ant-design/icons'
import 'bootstrap/dist/css/bootstrap.css'
import './App.css'
import ErrorBoundary from './ErrorBoundary'
let SurahSelection = []
let AyahSelection = []

class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      SurahNum: +localStorage.getItem('SurahNumber') || 1,
      AyahNum: +localStorage.getItem('VerseNumber') || 1,
      NumberofAyah: "",
      Translation: localStorage.getItem('TanslateTo') || 1,
      words: [],
      darkMode: localStorage.getItem('DarkMode') || false,
      ShowTranslations: false,
      Surahs: '',
      Loading: true,
      TranslatedQuran: '',
      Edition: '',
      theme: localStorage.getItem('Theme') || 'red',
      displayTheme: false,
    }
  }
  fetchData = () => {
    let url = this.state.Translation == 1 ? `https://khandakar227.github.io/json-data/bengali-quran/` :
      `https://khandakar227.github.io/json-data/en-sahih/`
    axios.get(`${url}surahs/${this.state.SurahNum}.json`).then(res => {
      this.setState({ TranslatedQuran: res.data })
    })
  }

  fetchTranslation = () => {
    let url = this.state.Translation == 1 ? `https://khandakar227.github.io/json-data/bengali-quran/` :
      `https://khandakar227.github.io/json-data/en-sahih/`
    axios.get(`${url}edition/edition.json`).then(res => {
      this.setState({ Edition: res.data })

    })
  }
  nextVerse = () => {
    if (this.state.AyahNum < this.state.Surahs[this.state.SurahNum - 1].numberOfAyahs) {
      this.setState({ AyahNum: this.state.AyahNum + 1 })
    } else if (this.state.AyahNum >= this.state.Surahs[this.state.SurahNum - 1].numberOfAyahs) {
      this.setState({ AyahNum: 1, SurahNum: this.state.SurahNum !== 114 ? this.state.SurahNum + 1 : 114 })
    }

  }
  prevVerse = () => {
    if (this.state.AyahNum < this.state.Surahs[this.state.SurahNum - 1].numberOfAyahs && this.state.AyahNum !== 1) {
      this.setState({ AyahNum: this.state.AyahNum - 1 })
    } else if (this.state.AyahNum === 1) {
      if (this.state.SurahNum <= 1) this.setState({ SurahNum: 1 })
      this.setState({ AyahNum: 1, SurahNum: this.state.SurahNum !== 1 ? this.state.SurahNum - 1 : 1 })
    }
  }

  handleKeyDown = (event) => {
    if (event.keyCode === 39) {
      this.nextVerse()
    } else if (event.keyCode === 37) {
      this.prevVerse()
    }
  }

  gotoAyah = (e) => {
    this.setState({ AyahNum: +e.target.value })
    document.activeElement.blur()
  }
  gotoSurah = (e) => {
    this.setState({ SurahNum: +e.target.value })
    this.setState({ AyahNum: 1 })
    document.activeElement.blur()
  }

  filterMode = () => {
    this.setState({ darkMode: !this.state.darkMode })
    localStorage.setItem('DarkMode', !this.state.darkMode)
  }
  showTr = () => { this.setState({ ShowTranslations: !this.state.ShowTranslations }) }
  setBN = () => { this.setState({ Translation: 1 }) }
  setEN = () => { this.setState({ Translation: 0 }) }

  async componentDidMount() {
    this.state.darkMode === 'true' ? this.setState({ darkMode: true }) : this.setState({ darkMode: false })
    await axios.get(`https://khandakar227.github.io/json-data/metadata/metadata.json`).then(res => {
      this.setState({ Surahs: res.data })
    })
    let db = firebase.database().ref(`/${this.state.SurahNum}`).child(this.state.AyahNum)

    this.fetchData()
    this.fetchTranslation()

    for (let i = 1; i <= this.state.Surahs[this.state.SurahNum - 1].numberOfAyahs; i++) {
      AyahSelection.push(<option key={i} value={i}>{i}</option>)
    }
    db.once('value', snap => {
      let words = snap.val().words
      this.setState({ words: words })
      this.setState({ Loading: false })
    })
    this.state.Surahs.map((Surah, i) => {
      SurahSelection.push(<option key={i + 1} value={i + 1}>{`${Surah.number}. ${Surah.englishName} (${Surah.englishNameTranslation})`}</option>)
      return SurahSelection
    })

    document.body.addEventListener('keydown', this.handleKeyDown);
  }

  componentDidUpdate(prevProps, prevState) {
    localStorage.setItem('TanslateTo', this.state.Translation)
    if (this.state.SurahNum !== prevState.SurahNum || this.state.AyahNum !== prevState.AyahNum || this.state.Translation !== prevState.Translation
      || this.state.theme !== prevState.theme) {
      localStorage.setItem('SurahNumber', this.state.SurahNum)
      localStorage.setItem('VerseNumber', this.state.AyahNum)
      localStorage.setItem('TanslateTo', this.state.Translation)
      localStorage.setItem('Theme', this.state.theme)
      this.setState({ Loading: true })
      this.fetchTranslation()
      this.fetchData()
      let db = firebase.database().ref(`/${this.state.SurahNum}`).child(this.state.AyahNum)
      db.once('value', snap => {
        let words = snap.val().words
        this.setState({ words: words })
        this.setState({ Loading: false })
      })
      if (this.state.SurahNum !== prevState.SurahNum) {
        AyahSelection = [];
        for (let i = 1; i <= this.state.Surahs[this.state.SurahNum - 1].numberOfAyahs; i++) {
          AyahSelection.push(<option key={i} value={i}>{i}</option>)
        }
      }
    }
  }
  keyDisable = () => document.body.removeEventListener('keydown', this.handleKeyDown)

  render() {
    return (
      <>
        <Navbar translation={this.state.Translation} theme={this.state.theme} />
        <div className="Theme-Container">
          <span className={this.state.theme+' btn'} onClick={() => this.setState({ displayTheme: !this.state.displayTheme })}>Theme</span>
          <div className="theme" style={{ display: this.state.displayTheme ? 'block' : 'none' }}>
            <button className="red rounded-circle color-btn" onClick={() => this.setState({ theme: 'red' })}></button>
            <button className="cornsilk rounded-circle color-btn" onClick={() => this.setState({ theme: 'cornsilk' })}></button>
            <button className="navyblue rounded-circle color-btn" onClick={() => this.setState({ theme: 'navyblue' })}></button>
            <button className="green rounded-circle color-btn" onClick={() => this.setState({ theme: 'green' })}></button>
            <button className="black rounded-circle color-btn" onClick={() => this.setState({ theme: 'black' })}></button>
            <button className="pink rounded-circle color-btn" onClick={() => this.setState({ theme: 'pink' })}></button>
          </div>
        </div>
        <div className="text-container">
          <div className="dropdown open">
            <button className={this.state.theme+" btn dropdown-toggle"} type="button" onClick={this.showTr}> Translate to </button>
            <div className="dropdown-menu" style={{ display: this.state.ShowTranslations ? 'block' : 'none' }}>
              <button className={"dropdown-item "+ (this.state.Translation == 1 ? this.state.theme:null)}
               onClick={this.setBN}>Bangla</button>
              <button className={"dropdown-item "+ (this.state.Translation != 1 ? this.state.theme : null)}
               onClick={this.setEN}>English</button>
            </div>
          </div>
          <div style={{ filter: this.state.darkMode ? 'invert(1)' : 'unset' }} className="jumbotron">
            <p style={{ top: '50%', left: '0', right: '0', fontWeight: 'bold', zIndex: '1', display: this.state.Loading ? 'block' : 'none' }}
             className={"position-fixed text-center "+this.state.theme}>
              <span className={"spinner-grow "+this.state.theme}></span> Loading...<br /> If loading is taking too long try refreshing again.</p>

            {this.state.TranslatedQuran && this.state.Edition ? <Ayah theme = {this.state.theme} words={this.state.words} SurahNum={this.state.SurahNum} AyahNum={this.state.AyahNum}
              TranslatedQuran={this.state.TranslatedQuran} Translation={this.state.Edition}
            /> : 'Loading'}

            <div className="text-center mt-3">
              <span className="mx-2">Verse:</span>
              <select className="custom-select VerseSelect"
                name="Verse" id="verses"
                onChange={(e) => this.gotoAyah(e)}
                onFocus={this.keyDisable}
                value={this.state.AyahNum}>
                {AyahSelection ? AyahSelection : null}
              </select>
              <span className="mx-2">Chapter:</span>
              <select className="custom-select ChapterSelect"
                name="Verse" id="verses"
                onFocus={this.keyDisable}
                onChange={(e) => this.gotoSurah(e)}
                value={this.state.SurahNum}>
                {SurahSelection ? SurahSelection : null}
              </select>
            </div>
          </div>
        </div>
        <Setting onClick={() => this.filterMode()} keyFunction={this.handleKeyDown} theme={this.state.theme} />
        <div onClick={() => this.nextVerse()} style={{ filter: this.state.darkMode ? 'invert(1)' : 'unset' }}
          className="position-fixed arrows next"><div><RightOutlined className={'arrow-clr '+this.state.theme}/></div></div>
        <div onClick={() => this.prevVerse()} style={{ filter: this.state.darkMode ? 'invert(1)' : 'unset' }}
          className="position-fixed arrows prev"><div><LeftOutlined className={'arrow-clr '+this.state.theme}/></div></div>
      </>
    )
  }
}

export default function AppWithErrorBoundary({ props }) {
  return (
    <ErrorBoundary>
      <App {...props} />
    </ErrorBoundary>
  )
}
